/* Local Google Fonts - Muli Family */
/* muli-200 - latin */
@font-face {
    font-family: "Muli";
    font-style: normal;
    font-weight: 200;
    src: url("../fonts/muli-v12-latin-200.eot");
    /* IE9 Compat Modes */
    src: local("Muli ExtraLight"), local("Muli-ExtraLight"),
      url("../fonts/muli-v12-latin-200.eot?#iefix") format("embedded-opentype"),
      url("../fonts/muli-v12-latin-200.woff2") format("woff2"),
      url("../fonts/muli-v12-latin-200.woff") format("woff"),
      url("../fonts/muli-v12-latin-200.ttf") format("truetype"),
      url("../fonts/muli-v12-latin-200.svg#Muli") format("svg");
    /* Legacy iOS */
  }
  
